.selectedPage {
  font-weight: bold;
  font-size: 20px;
}

.wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  .userPhoto {
    width: 200px;
    height: 200px;
  }

  .wrapperContent {
    margin-left: 20px;

    .name {
      background: linear-gradient(90deg, #cd122d, #154284);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      color: #fff;
      font-size: 2rem;
      font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
    }

    .status {
      text-decoration: none;
      color: #154284;
      font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
      font-weight: 400;
      font-style: italic;
      border: none;
      font-size: 1.8rem;
      line-height: 2.2rem;
      background: linear-gradient(90deg, #154284, #154284);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    .button {
      text-align: center;
      text-transform: uppercase;
      cursor: pointer;
      font-size: 0.875rem;
      font-family: "Roboto", "Helvetica", "Arial", sans-serif;
      font-weight: 500;
      line-height: 1.75;
      min-width: 64px;
      letter-spacing: 0.02857em;
      position: relative;
      background-color: #154284;
      border: none;
      color: #fff;
      padding: 6px 16px;
      width: 110px;
      transition-duration: 0.4s;
      overflow: hidden;
      box-shadow: 0 5px 15px #193047;
      border-radius: 4px;
      margin: 10px;

      &:hover {
        background: #fff;
        box-shadow: 0 2px 10px 5px #154284;
        color: #000;
      }

      &:after {
        content: "";
        background: #154284;
        display: block;
        position: absolute;
        padding-top: 300%;
        padding-left: 350%;
        margin-left: -20px !important;
        margin-top: -120%;
        opacity: 0;
        transition: all 0.8s
      }

      &:active:after {
        padding: 0;
        margin: 0;
        opacity: 1;
        transition: 0s
      }

      &:focus {
        outline: 0;
      }
    }
  }


}





